import { Injectable }    from '@angular/core';
import { Headers, Http } from '@angular/http';

const URL = 'https://webphim.herokuapp.com/product/';
const URL_cm = 'https://webphim.herokuapp.com/comment/';
const URL_user = 'https://webphim.herokuapp.com/user/';
@Injectable()
export class VideoService{
    constructor(private http: Http){}
    // lay tat ca video
    getVideo(){
        return this.http.get(URL+"get").map(res => res.json());
    }
    // lay 1 video
    getvideoOne( id:String ){
        
        return  this.http.get(URL+"get/"+id).map(res => res.json());
    }
    // Lay video qa the loai 
    getVideoFilde(theloai: String){
        return this.http.get(URL+"get-filde/"+theloai).map(res => res.json());
    }
    // Lay video qa quoc gia
    getVideoNation(quocgia: String){
        return this.http.get(URL+"nation/"+quocgia).map(res => res.json());
    }
    // Tìm kiếm video qua ten
    getVideoname(ten : String){
        
        return this.http.get(URL+"search/"+ten).map(res => res.json());
    }
    // Lấy tất cả comment về 1 video ra 

    getCommnet(id: String){
        
        return this.http.get(URL_cm+"get/"+id).map(res => res.json());
    }
    // Them comment ve 1 video
    postComment(comment : any){
        let headers = new Headers();
        headers.append("Content-Type", "application/json");
        
        return this.http.post(URL_cm+"post/comment",JSON.stringify(comment),{headers: headers}).map(res => res.json());
    }
    // Đăng nhập
    SignIn(login : any){
        let headers = new Headers();
        headers.append("Content-Type", "application/json");
        return this.http.post(URL_user+"signin",JSON.stringify(login),{headers: headers}).map(res => res.json());
    }
    SignUp(signup:any){
        let headers = new Headers();
        headers.append("Content-Type", "application/json");
        return this.http.post(URL_user+"signup",JSON.stringify(signup),{headers: headers}).map(res => res.json());
    }
}


