import { Component, OnInit } from '@angular/core';
import { Router }            from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';


import { ValidateService } from "../validate.service";
import { VideoService } from "../sevice/video.serivce";

@Component({
  selector: 'app-dangky',
  templateUrl: './dangky.component.html',
  styleUrls: ['./dangky.component.css']
})
export class DangkyComponent implements OnInit {

  username:  String;
  password: String;
  email : String;
  password_again : String;
  check = false;
  constructor(
    private router: Router,
    private validateSevice: ValidateService,
    private videoService : VideoService,
    private _flashMessagesService: FlashMessagesService
  ) { }

  ngOnInit() {
  }
  SignUp(){
    var signup = {
      username: this.username,
      password : this.password,
      email : this.email
    }
    
    if(this.validateSevice.validateSignUp(signup)){
      if(this.password_again === this.password && this.password.length > 6){
        if(this.validateSevice.validateEmail(this.email)){
          this.videoService.SignUp(signup).subscribe(res =>{
            if(res.success){
               this._flashMessagesService.show(res.msg, { cssClass: 'alert-success', timeout: 2000 });
               this.username = "";
               this.password = "";
               this.email  = "";
               this.router.navigate(['/']);
            }else{
               this._flashMessagesService.show(res.msg, { cssClass: 'alert-danger', timeout: 2000 });
            }
          })
        }else{
          this._flashMessagesService.show("Email không hợp lệ", { cssClass: 'alert-danger', timeout: 2000 });
        }
      }else{
        this._flashMessagesService.show("Password không trùng khớp", { cssClass: 'alert-danger', timeout: 2000 });
      }
    }else{
      
      this._flashMessagesService.show("Bạn nhập thiếu thông tin", { cssClass: 'alert-danger', timeout: 2000 });
    }
  }

}



