import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Location }               from '@angular/common';
import {VideoService} from "../sevice/video.serivce";
@Component({
  selector: 'app-xemvideotheoloai',
  templateUrl: './xemvideotheoloai.component.html',
  styleUrls: ['./xemvideotheoloai.component.css']
})
export class XemvideotheoloaiComponent implements OnInit {
  video: any[];
  constructor(
    private route: ActivatedRoute,
    private location: Location,
    private videoSerive : VideoService
  ) { }

  ngOnInit() {
    this.route.params
    .switchMap((params: Params) => this.videoSerive.getVideoFilde(params['id']))
      .subscribe(video =>{
        this.video = video;
      });
  }

}



