import { Component, OnInit } from '@angular/core';
import { Router }            from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';


import { ValidateService } from "../validate.service";
import { VideoService } from "../sevice/video.serivce";

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  tim : String;
  username:  String;
  password: String;
  check = false;
  constructor(
    private router: Router,
    private validateSevice: ValidateService,
    private videoService : VideoService,
    private _flashMessagesService: FlashMessagesService
  ) { }

  ngOnInit() {
    // kim  tra neu co ton tai thi gan bien de dang xuat

    if(localStorage.getItem("User") && localStorage.getItem("Email")){
      this.check = true;
    }
  }
  search(){
    var giatri = this.tim;
    let link = ['/tim-video', giatri];
    this.router.navigate(link);
    this.tim = "";
  }
  Login(){
    var login = {
      username : this.username,
      password : this.password
    }
    // Check Form
    if(!this.validateSevice.validateLogin(login)){
      this._flashMessagesService.show("Bạn nhập thiếu thông tin", { cssClass: 'alert-danger', timeout: 2000 });
    }else{
      // Gui dư liệu đến nodejs
      this.videoService.SignIn(login).subscribe(res => {
        if (res.success) {
           this._flashMessagesService.show("Đăng nhập thành công", { cssClass: 'alert-success', timeout: 2000 });
           this.check = true;
           // Tao du lieu Username va email tren localStorage
           localStorage.setItem("User", res.username);
           localStorage.setItem("Email",res.email);
           this.router.navigate(["/"]);
        }else{
           this._flashMessagesService.show("Đăng nhập thất bại", { cssClass: 'alert-danger', timeout: 2000 });
        }
      });
    }
  }
  Logout(){
    this.check = false;
    localStorage.removeItem("User");
    localStorage.removeItem("Email");
  }
}



