import { Component, ElementRef, Input, OnInit } from '@angular/core';
import { FileUploader } from 'ng2-file-upload/ng2-file-upload';
import { Http, Response } from '@angular/http';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import "rxjs/add/operator/do";
import "rxjs/add/operator/map";
const URL = 'https://webphim.herokuapp.com/product/addvideo';
@Component({
  selector: 'app-postvideo',
  templateUrl: './postvideo.component.html',
  styleUrls: ['./postvideo.component.css']
})
export class PostvideoComponent implements OnInit {
  public uploader: FileUploader = new FileUploader({ url: URL, itemAlias: 'photo' });
  ten: String;
  link: String;
  hinh: String;
  the_loai: String;
  quoc_gia: String;
  dien_vien: String;
  thoi_luong: String;
  nam_phat_hanh: String;
  public edited_hinh = false;
  public edited_thongtin = false;
  ngOnInit() {
    this.uploader.onAfterAddingFile = (file) => { file.withCredentials = false; };
  }
  constructor(private http: Http,
    private el: ElementRef,
    private _flashMessagesService: FlashMessagesService,
    private router: Router) {
  }
  upload() {
    let inputEl: HTMLInputElement = this.el.nativeElement.querySelector('#video');
    let fileCount: number = inputEl.files.length;
    let link_video_lan_dau = JSON.stringify(inputEl.files[0].name);
    //xoa " lan dau
    let link_video_lan_sau = link_video_lan_dau.replace('"', "");
    //xoa " lan sau
    this.link = link_video_lan_sau.replace('"', "");

    let formData = new FormData();
    if (fileCount > 0) { // a file was selected
      formData.append('photo', inputEl.files.item(0));

      this.http
        .post(URL, formData).map((res: any) => res).subscribe(
        (success) => {
          //alert(success._body);
          this._flashMessagesService.show(success._body, { cssClass: 'alert-success', timeout: 2000 });
        },
        (error) => {
          //alert(error)
          this._flashMessagesService.show("Thêm video không thành công", { cssClass: 'alert-danger', timeout: 2000 });
        }
        );
    }
  }
  upload_hinh() {
    let inputEl: HTMLInputElement = this.el.nativeElement.querySelector('#hinh');
    let fileCount: number = inputEl.files.length;
    let link_hinh_lan_dau = JSON.stringify(inputEl.files[0].name);
    //xoa " lan dau
    let link_hinh_lan_sau = link_hinh_lan_dau.replace('"', "");
    //xoa " lan sau
    this.hinh = link_hinh_lan_sau.replace('"', "");

    let formData = new FormData();
    if (fileCount > 0) { // a file was selected
      formData.append('hinh', inputEl.files.item(0));

      this.http
        .post("https://webphim.herokuapp.com/addhinh", formData).map((res: any) => res).subscribe(
        (success) => {
          //          alert(success._body);
          this._flashMessagesService.show(success._body, { cssClass: 'alert-success', timeout: 2000 });
        },
        (error) => {
          //alert(error)
          this._flashMessagesService.show("Thêm hình đại diện không thành công", { cssClass: 'alert-danger', timeout: 2000 });
        }
        );
    }
  }
  showHinh(): void {
    this.edited_hinh = true;
  }
  showThongtin(): void {
    this.edited_thongtin = true;
  }
  onAddProductSubmit() {
    var product = {
      ten: this.ten,
      the_loai: this.the_loai,
      hinh: this.hinh,
      link: this.link,
      quoc_gia: this.quoc_gia,
      dien_vien: this.dien_vien,
      thoi_luong: this.thoi_luong,
      nam_phat_hanh: this.nam_phat_hanh
    }

    this.http.post("https://webphim.herokuapp.com/product/add", product).map((res: any) => res).subscribe(
      (success) => {


        this.router.navigate(['/']);
        this._flashMessagesService.show("Thêm thông tin mô tả thành công", { cssClass: 'alert-success', timeout: 2000 });
      },
      (error) => {
        this._flashMessagesService.show("Thêm thông tin mô tả không thành công", { cssClass: 'alert-danger', timeout: 2000 });
      }
    );
  }

}



