import { Component, OnInit } from '@angular/core';
import {VideoService} from "../sevice/video.serivce";
@Component({
  selector: 'app-video',
  templateUrl: './video.component.html',
  styleUrls: ['./video.component.css']
})
export class VideoComponent implements OnInit {
  video : any[];
  haihuoc: any[];
  kinhdi: any[];
  tinhcam: any[];
  tamly: any[];
  hoathinh: any[];
  constructor(private videoService : VideoService) { }

  ngOnInit() {
    // Tat ca video
    this.videoService.getVideo().subscribe(video =>{
      this.video = video.slice(0, 9);
      
    })
    // video hai huoc qua thong tin la hai huoc
    this.videoService.getVideoFilde("hai-huoc").subscribe(haihuoc=>{
      this.haihuoc= haihuoc.slice(0,4);
    });

    // video kinh di qua thong tin la kinh di
    this.videoService.getVideoFilde("kinh-di").subscribe(kinhdi=>{
      this.kinhdi= kinhdi.slice(0,4);
    });

    // video tinh cam qua thong tin la tinh cam
    this.videoService.getVideoFilde("tinh-cam").subscribe(tinhcam=>{
      this.tinhcam= tinhcam.slice(0,4);
    });

    // video tam ly qua thong tin la tam ly
    this.videoService.getVideoFilde("tam-ly").subscribe(tamly=>{
      this.tamly= tamly.slice(0,4);
    });

    // video hoat hinh qua thong tin la hoat hinh
    this.videoService.getVideoFilde("hoat-hinh").subscribe(hoathinh=>{
      this.hoathinh= hoathinh.slice(0,4);
    });
  }
  

}



