import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Location } from '@angular/common';
import { ChatService } from './videodetail.service';

import { VideoService } from "../sevice/video.serivce";
import { ValidateService } from "../validate.service";

import 'rxjs/add/operator/switchMap';
@Component({
  selector: 'app-videodetail',
  templateUrl: './videodetail.component.html',
  styleUrls: ['./videodetail.component.css'],
  providers: [ChatService]
})
export class VideodetailComponent implements OnInit {
  messages = [];
  connection;
  message;
  ten_web :String;
  ten_web_socket ; String;
  // info form commet
  name: String;
  email: String;
  comment: String;
  // disable button commnet
  show_err = false;
  edited_hinh = false;

  // info show value to html
  id_video: String
  video: any[];
  comment_list: any[];
  linhvuc: String;
  phimkhac: any[];
  constructor(private route: ActivatedRoute,
    private location: Location,
    private videoSerive: VideoService,
    private validateSevice: ValidateService,
    private chatService:ChatService) { }

  ngOnInit() {
    this.route.params
      .switchMap((params: Params) => this.videoSerive.getvideoOne(params['id']))
      .subscribe(video => {
        this.video = video;
        this.id_video = video[0]._id;
        //lay id web site cho viec binh luan
        this.ten_web = this.id_video;
        // lay phim khac theo the loai tu video
        this.videoSerive.getVideoFilde(video[0].the_loai).subscribe(phimkhac => {
          this.phimkhac = phimkhac
        })
        this.videoSerive.getCommnet(this.id_video).subscribe(comment_list => {
          this.comment_list = comment_list;
        })
      });
    if(localStorage.getItem("User") && localStorage.getItem("Email")){
      this.name = localStorage.getItem("User");
      this.email = localStorage.getItem("Email");
    }else{
      this.name = "";
      this.email = "";
    }

    this.connection = this.chatService.getMessages().subscribe(message => {
      
      this.ten_web_socket = message["web"];
      // kiem tra neu 2 id website trung nhau thi them vao mang
      // id web goc va id web duoc nhan tu socket.io
      if(this.ten_web === this.ten_web_socket){
        this.messages.push(message["text"]);
      }
    })
  }
  binh_luan() {
    var contentComment = {
      name: this.name,
      email: this.email,
      comment: this.comment,
      id_video: this.id_video
    }
    // ADD comment
    if (this.validateSevice.validateComment(contentComment) && this.validateSevice.validateEmail(contentComment.email)) {
      this.videoSerive.postComment(contentComment).subscribe(res => {
        if (res) {
          this.chatService.sendMessage(contentComment,this.ten_web);
        }
      });
    } else {
      this.show_err = !this.show_err;
      setTimeout(() => {    //<<<---    using ()=> syntax
        this.show_err = false;
      }, 5000);
    }
    this.comment = "";
  }
  showButton(): void {
    this.edited_hinh = !this.edited_hinh;
    setTimeout(() => {    //<<<---    using ()=> syntax
      this.edited_hinh = false;
    }, 5000);
  }


}



