import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Location }               from '@angular/common';
import {VideoService} from "../sevice/video.serivce";

@Component({
  selector: 'app-xemvideotheoquocgia',
  templateUrl: './xemvideotheoquocgia.component.html',
  styleUrls: ['./xemvideotheoquocgia.component.css']
})
export class XemvideotheoquocgiaComponent implements OnInit {
 video: any[];
  constructor(
    private route: ActivatedRoute,
    private location: Location,
    private videoSerive : VideoService
  ) { }

  ngOnInit() {
    this.route.params
    .switchMap((params: Params) => this.videoSerive.getVideoNation(params['id']))
      .subscribe(video =>{
        this.video = video;
      });
  }

}



