import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import { FileSelectDirective } from 'ng2-file-upload';
import { FlashMessagesModule } from 'angular2-flash-messages';

import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { VideoComponent } from './video/video.component';
import {VideoService} from "./sevice/video.serivce";
import { FooterComponent } from './footer/footer.component';
import { PostvideoComponent } from './postvideo/postvideo.component';
import { VideodetailComponent } from './videodetail/videodetail.component';
import { XemvideotheoloaiComponent } from './xemvideotheoloai/xemvideotheoloai.component';
import { XemvideotheoquocgiaComponent } from './xemvideotheoquocgia/xemvideotheoquocgia.component';
import { TimvideoComponent } from './timvideo/timvideo.component';
import { ValidateService} from "./validate.service";
import { DangkyComponent } from './dangky/dangky.component';

const routes: Routes = [
  { path: '', redirectTo: 'video', pathMatch: 'full' },
  { path: 'postvideo',  component: PostvideoComponent},
   { path: 'video',  component: VideoComponent },
   { path: 'video/:id',  component: VideodetailComponent },
   { path: 'xem-video-the-loai/:id',  component: XemvideotheoloaiComponent },
   { path: 'xem-video-theo-quoc-gia/:id',  component: XemvideotheoquocgiaComponent  },
   { path: 'tim-video/:id',  component: TimvideoComponent  },
   { path: 'signup',  component: DangkyComponent  },
   
];
@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    VideoComponent,
    FooterComponent,
    PostvideoComponent,
    FileSelectDirective,
    VideodetailComponent,
    XemvideotheoloaiComponent,
    XemvideotheoquocgiaComponent,
    TimvideoComponent,
    DangkyComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(routes),
    FlashMessagesModule
  ],
  providers: [ValidateService,VideoService],
  bootstrap: [AppComponent]
})
export class AppModule { }



