import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { Location }               from '@angular/common';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router }            from '@angular/router';
import {VideoService} from "../sevice/video.serivce";

@Component({
  selector: 'app-timvideo',
  templateUrl: './timvideo.component.html',
  styleUrls: ['./timvideo.component.css']
})
export class TimvideoComponent implements OnInit {
  video: any[];
   constructor(
    private route: ActivatedRoute,
    private location: Location,
    private videoSerive : VideoService,
    private _flashMessagesService: FlashMessagesService,
    private router: Router
    
  ) { }

  ngOnInit() {
    this.route.params
    .switchMap((params: Params) => this.videoSerive.getVideoname(params['id']))
      .subscribe(video =>{
        
        if(video.success){
          this.video = video.docs;
          
        }else{
          this._flashMessagesService.show("Không tìm thấy video này", { cssClass: 'alert-danger', timeout: 3000 });
          let link = ['/'];
          this.router.navigate(link);
        }
      });
  }

}



